<script>
import { required, maxLength, minValue, url } from 'vuelidate/lib/validators';
import { VueEditor } from 'vue2-editor';

export default {
  name: 'CourseNewEdit',
  components: { VueEditor },
  data() {
    return {
      prevRoute: false,
      updateCover: false,
      week: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
      ],
      category: ['', '', '', '', ''],
      coaches: [
        'Amy',
        'CICI',
        'Danny',
        'Papaya',
        'Peco',
        'Ryan',
        '',
        '',
        '',
        '',
        'Ni',
        '',
      ],
      courseNew: {
        title: '',
        intro: '',
        tags: [],
        categorySelected: '',
        coach: '',
        week: '',
        time: '',
        quantity: null,
        remainQuantity: null,
        originPrice: null,
        price: null,
        discount: false,
        imageUrl: '',
        avgRating: 0,
      },
      course: null,
      tag: null,
      modal: null,
      titleMax: 25,
      introMax: 80,
    };
  },
  validations: {
    course: {
      title: {
        required,
        maxLength: maxLength(24),
      },
      intro: {
        required,
        maxLength: maxLength(79),
      },
      categorySelected: {
        required,
      },
      coach: {
        required,
      },
      week: { required },
      time: {
        required,
      },
      originPrice: { required, minValue: minValue(0) },
      price: { required, minValue: minValue(0) },
      quantity: { required, minValue: minValue(0) },
      remainQuantity: { required, minValue: minValue(0) },
      imageUrl: { required, url },
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
    status() {
      return this.$store.state.courses.status;
    },
    getCourse() {
      return this.$store.state.courses.course;
    },
    getDownloadURL() {
      return this.$store.state.downloadURL;
    },
  },
  watch: {
    // 
    status() {
      if (this.status === '') {
        this.course = { ...this.courseNew };
      }
    },
    getDownloadURL() {
      if (this.getDownloadURL) {
        this.course.imageUrl = this.getDownloadURL;
        this.updateCover = false;
      }
    },
  },
  methods: {
    addCourse() {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.$toasted.error('', { duration: 3000 });
      } else {
        this.$store
          .dispatch('courses/createCourse', { ...this.course })
          .then(() => {
            this.$toasted.success('', {
              duration: 2000,
            });
            this.$router.push({ name: 'coursesAdmin' });
          })
          .catch((e) => console(e.message));
      }
    },
    updateCourse(id) {
      this.$v.$touch();
      if (!this.updateCover) {
        if (this.$v.$invalid) {
          this.$toasted.error('', { duration: 3000 });
        } else {
          this.$store
            .dispatch('courses/updateCourse', { id, course: this.course })
            .then(() => {
              this.$toasted.success('!!', {
                duration: 2000,
              });
              this.$router.push({ name: 'coursesAdmin' });
            });
        }
      }
    },
    addTags() {
      if (this.tag) {
        this.course.tags.push(this.tag);
      }
      this.tag = '';
    },
    deleteTag(key) {
      this.course.tags.splice(key, 1);
    },
    uploadImage(e) {
      const file = e.target.files[0];
      if (!file.type.includes('image')) {
        this.$toasted.error('', { duration: 3000 });
      } else {
        this.updateCover = true;
        this.$store.dispatch('updateFile', { file, place: 'courses/' });
      }
    },
  },
  created() {
    this.$store.commit('setDownloadUrl', null);

    if (this.status === '') {
      this.course = { ...this.getCourse };
    } else {
      this.course = { ...this.courseNew };
    }
  },
};
</script>

